<template>
  <transition name="modal">
    <div v-if="show" class="modal-overlay" @click.self="closeModal">
      <div class="modal-container">
        <button class="close-btn" @click="closeModal">×</button>
        <h1>볼더링 하는 법</h1>
        <p class="subtitle">기초부터 차근차근 알아봐요</p>

        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progressPercent + '%' }"></div>
        </div>

        <div class="content-screen">
            <div :key="currentSlide" class="slide">
              <div class="media-container">
                <button
                  class="nav-btn nav-btn-left"
                  :disabled="currentSlide === 0"
                  @click="prevSlide"
                  aria-label="이전"
                >
                  ‹
                </button>
                <img
                  :src="publicPath + 'images/' + slides[currentSlide].image"
                  :alt="'볼더링 하는 법 ' + (currentSlide + 1)"
                  class="slide-image"
                />
                <button
                  class="nav-btn nav-btn-right"
                  :disabled="currentSlide === slides.length - 1"
                  @click="nextSlide"
                  aria-label="다음"
                >
                  ›
                </button>
              </div>

              <div class="slide-description" v-html="slides[currentSlide].description"></div>
            </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
  name: 'HowToClimbModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const currentSlide = ref(0)
    const publicPath = process.env.BASE_URL

    const slides = [
      {
        description: '"홀드"는 우리가 잡고 밟는 돌을 통칭합니다.<br>"볼륨"은 그중에서도 거대한 부피의 홀드를 의미합니다.<br>이 홀드들을 타고 올라가 TOP에 도달해봅시다!',
        image: 'how-to-1.jpg'
      },
      {
        description: '가장 먼저 내가 풀 문제의 START와 TOP을 확인합니다.<br>난이도는 테이프 색상이며 홀드 색상과는 별개입니다.<br>회색 난이도의 노란홀드, 파란색 난이도의 파란홀드 이런 식입니다.',
        image: 'how-to-2.jpg'
      },
      {
        description: '회색 난이도의 노란홀드 문제를 봅시다. 우선 루트를 파악합니다.<br>어떻게 올라가야할지 찾아보는 것을 "루트파인딩" 이라고 합니다.<br>문제를 풀기 전 다른사람과 루트가 겹치는 지 꼭 확인해야합니다.',
        image: 'how-to-3.jpg'
      },
      {
        description: '자 이제부터는 내 문제에 해당하는 색상의 홀드만 사용합니다.<br>등반 중 다른 홀드를 잡거나 밟는 것을 "댑"이라고 하는데,<br>이 경우 문제를 풀었다고 인정되지 않습니다. 실수로 하면 억울...<br>',
        image: 'how-to-4.jpg'
      },
      {
        description: '매트에서 두 발이 다 떨어진 상태에서 버텨야 START 입니다.<br>발을 떼자마자 바로 다음 홀드를 잡으면 안 됩니다.<br>발을 벽에 대거나 공중에 띄워도 됩니다.',
        image: 'how-to-5.jpg'
      },
      {
        description: '루트파인딩을 떠올리며 열심히 등반합니다.<br>참고로 볼륨에 해당 색상의 홀드가 박혀있다면<br>그 볼륨 전체를 사용할 수 있습니다. 잘 활용합시다.',
        image: 'how-to-6.jpg'
      },
      {
        description: 'TOP 홀드를 양손으로 잡고 3초간 유지하면 완등입니다!<br>START 때와 마찬가지로 완전히 제압해서 버텨야합니다.<br>완등 후 바로 매트로 뛰어내리면 위험합니다.',
        image: 'how-to-7.jpg'
      },
      {
        description: '"다운클라이밍" 때는 아무 홀드를 잡고 내려와도됩니다.<br>중간중간 잡기 좋은 "다운홀드"들을 배치해두므로 사용하면 좋습니다.<br>매트 가까이까지 내려와서 양발로 착지해주세요.',
        image: 'how-to-8.jpg'
      }
    ]

    const progressPercent = computed(() => {
      return ((currentSlide.value + 1) / slides.length) * 100
    })

    const closeModal = () => {
      currentSlide.value = 0
      emit('close')
    }

    const nextSlide = () => {
      if (currentSlide.value < slides.length - 1) {
        currentSlide.value++
      }
    }

    const prevSlide = () => {
      if (currentSlide.value > 0) {
        currentSlide.value--
      }
    }

    const handleKeydown = (e) => {
      if (!props.show) return

      if (e.key === 'Escape') closeModal()
      if (e.key === 'ArrowRight') nextSlide()
      if (e.key === 'ArrowLeft') prevSlide()
    }

    onMounted(() => {
      document.addEventListener('keydown', handleKeydown)
    })

    onUnmounted(() => {
      document.removeEventListener('keydown', handleKeydown)
    })

    return {
      currentSlide,
      slides,
      progressPercent,
      publicPath,
      closeModal,
      nextSlide,
      prevSlide
    }
  }
}
</script>

