<template>
  <div class="store-info-wrapper">
    <div class="store-info">
      <div class="background-layer" :style="backgroundStyle"></div>
      <StoreItem 
          v-if="currentStore" 
          :store="currentStore" 
          :key="currentStore.id + '-' + Date.now()"
      />
    </div>
  </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { computed, watchEffect } from 'vue'
import { stores } from '@/stores/climbStore'
import { headerStore } from '@/stores/commonStore'
import StoreItem from '@/components/StoreItem.vue'

export default {
  name: 'StoreInfo',
  components: {
    StoreItem
  },
  setup() {
    const route = useRoute()
  
    const id = parseInt(route.params.id)
    
    const currentStore = computed(() => {
      const store = stores.find(store => store.id === id)
      return store || null
    })

    const backgroundStyle = computed(() => {
      if (!currentStore.value?.imagePath) {
        return {}
      }
      return {
        backgroundImage: `url(${currentStore.value.imagePath})`,
        backgroundPosition: 'center',
        backgroundSize: '50%',
        backgroundRepeat: 'no-repeat',
        opacity: '0.5'
      }
    })

    watchEffect(() => {
      if (currentStore.value) {
        headerStore.title = currentStore.value.title
      }
    })
    
    return {
      route,
      currentStore,
      backgroundStyle,
    }
  }
}
</script>

<style>
.store-info-wrapper {
  display: flex;
  justify-content: center;
  flex: 1;
}

.store-info {
  padding: 16px;
  box-sizing: border-box;
  position: relative;
  flex: 1;
  max-width: 900px;
}

.store-info p {
  white-space: pre-line;
}

.background-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
</style>