<template>
  <article class="branch-item"
       :key="branch.id"
       :class="{ closed: branch.isClosed }"
       :style="{ borderColor: branch.color || '#e0e0e0' }">

    <span v-if="branch.isClosed" class="closed-badge">[ 폐점 ]</span>
    <div v-if="branch.isClosed" class="x-mark">✕</div>

    <div v-show="!branch.isClosed" :id="mapId" class="naver-map"></div>

    <div class="title">
      <h3>{{ storeTitle }} {{ branch.name }}</h3>

      <a v-if="!branch.isClosed"
         :href="branch.instagram"
         target="_blank"
         class="corner-logo-link"
         rel="noopener">
        <img :src="publicPath + 'images/instagram_icon.png'" alt="Instagram" class="corner-logo" />
      </a>
      <div v-else class="corner-logo-disabled">
        <img :src="publicPath + 'images/instagram_icon.png'" alt="Instagram" class="corner-logo" />
      </div>
    </div>

    <p class="address">
      📍&nbsp;
      <a v-if="!branch.isClosed" :href="branch.map" class="clickable-link" target="_blank" rel="noopener noreferrer">
        {{ branch.address }}
      </a>
      <span v-else class="disabled-text">{{ branch.address }}</span>
    </p>
    <p class="tel">
      📞&nbsp;
      <a v-if="!branch.isClosed" :href="`tel:${branch.telephone}`" class="clickable-link">
        {{ branch.telephone }}
      </a>
      <span v-else class="disabled-text">{{ branch.telephone }}</span>
    </p>
    <p class="option">🔍&nbsp;&nbsp;{{ branch.option }}</p>
    <p class="parking">🅿️&nbsp;&nbsp;{{ branch.parking }}</p>
    <p class="comment" v-if="settingStore.showTMI && branch.comment"><b>
      🗣️&nbsp;&nbsp;만든이: {{ branch.comment }}
    </b></p>
  </article>
</template>

<script>
import { settingStore } from '@/stores/commonStore';
import { nextTick } from 'vue';

let naverLoader;

// 네이버 지도 스크립트 로더(중복 방지)
function loadNaverMap(clientId) {
  if (window.naver?.maps) return Promise.resolve();
  if (!naverLoader) {
    naverLoader = new Promise((resolve, reject) => {
      const s = document.createElement('script');
      s.async = true;
      s.defer = true;
      s.src = `https://oapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${encodeURIComponent(clientId)}`;
      s.onload = () => (window.naver?.maps ? resolve() : reject(new Error('Naver Maps not ready')));
      s.onerror = () => reject(new Error('Naver Maps script load error'));
      document.head.appendChild(s);
    });
  }
  return naverLoader;
}

// 네이버 지도 객체 완전 준비 대기 (사파리 레이스 방지)
function waitForNaverReady(timeoutMs = 5000) {
  const start = Date.now();
  return new Promise((resolve, reject) => {
    (function tick() {
      const ok = !!(window.naver &&
                    window.naver.maps &&
                    typeof window.naver.maps.LatLng === 'function' &&
                    typeof window.naver.maps.Map === 'function');
      if (ok) return resolve();
      if (Date.now() - start > timeoutMs) {
        return reject(new Error('Naver Maps not fully ready'));
      }
      setTimeout(tick, 50);
    })();
  });
}

// 요소 가시성 체크
function isVisible(el) {
  if (!el) return false;
  const rect = el.getBoundingClientRect();
  const style = window.getComputedStyle(el);
  return rect.width > 0 && rect.height > 0 && style.visibility !== 'hidden' && style.display !== 'none';
}

export default {
  name: 'BranchItem',
  props: {
    branch: { type: Object, required: true },
    storeTitle: { type: String, required: true },
    ncpClientId: { type: String, default: '' }
  },
  data() {
    return {
        settingStore,
        publicPath: process.env.BASE_URL,
        mapId: `map-${this.branch.id}`,
        map: null,
        marker: null,
        resizeObserver: null,
        onPageShowHandler: null,
        resizeFixTimer: null,
        disposed: false,
        touchStartCap: null,
        touchMoveCap: null,
    };
  },
  async mounted() {
    if (this.branch.isClosed) return;

    const clientId = this.ncpClientId || 'rsd9neufw7';
    try {
      await loadNaverMap(clientId);
      await waitForNaverReady(5000);
    } catch (e) {
      console.error('[NaverMapLoad]', e);
      return;
    }

    await nextTick();

    const mapDiv = document.getElementById(this.mapId);
    if (!mapDiv) return;

    const start = Date.now();
    while (!isVisible(mapDiv) && Date.now() - start < 2000) {
      await new Promise(r => setTimeout(r, 50));
    }

    const hostNow = document.getElementById(this.mapId);
    if (!hostNow || hostNow !== mapDiv) return;
    if (this.map) return;

    const lat = parseFloat(this.branch.coordinates?.lat ?? this.branch.lat);
    const lng = parseFloat(this.branch.coordinates?.lng ?? this.branch.lng);
    if (isNaN(lat) || isNaN(lng)) {
      console.error('좌표가 올바르지 않습니다.');
      return;
    }

    if (!window.naver?.maps?.LatLng) {
      try { await waitForNaverReady(2000); } catch (e) { console.error(e); return; }
    }

    let center;
    try {
      center = new window.naver.maps.LatLng(lat, lng);
    } catch (e) {
      console.error('[LatLngCreate]', e);
      return;
    }

    function isIOSMobileSafari() {
        const ua = navigator.userAgent || '';
        const isIOS = /(iPad|iPhone|iPod)/.test(ua);
        const isSafari = /Safari/.test(ua) && !/CriOS|FxiOS|OPiOS|EdgiOS/.test(ua);
        return isIOS && isSafari;
    }

    // 지도 생성: 확대/축소 제스처 비활성화
    try {
      this.map = new window.naver.maps.Map(mapDiv, {
        center,
        zoom: 16,
        scrollWheel: false,              // 휠 줌 금지
        // pinchZoom: false,                // 핀치 줌 금지
        // disableDoubleTapZoom: true,      // 더블탭 줌 금지
        // disableDoubleClickZoom: true,    // 더블클릭 줌 금지
        // keyboardShortcuts: false,        // 키보드 줌 금지
        draggable: !isIOSMobileSafari()  // 드래그 가능여부
      });
      this.marker = new window.naver.maps.Marker({ position: center, map: this.map });
    } catch (e) {
      console.error('[MapCreate]', e);
      return;
    }

    // iOS 사파리 렌더 보정 타이머
    this.resizeFixTimer = setTimeout(() => {
      if (this.disposed || !this.map) return;
      const host = document.getElementById(this.mapId);
      if (!host) return;
      try {
        window.naver.maps.Event.trigger(this.map, 'resize');
        this.map.setCenter(center);
      } catch (e) {
        console.error(e);
      }
    }, 120);

    // 렌더 보정 루프(최대 1초)
    const ensureRendered = async (tries = 10) => {
      for (let i = 0; i < tries; i++) {
        if (this.disposed || !this.map) return;
        const host = document.getElementById(this.mapId);
        if (!host) return;
        const w = host.offsetWidth, h = host.offsetHeight;
        try {
          window.naver.maps.Event.trigger(this.map, 'resize');
          this.map.setCenter(center);
        } catch (e) { console.error(e); }
        if (w > 10 && h > 10) {
          window.naver.maps.Event.addListener(this.map, 'idle', () => {
            if (this.disposed || !this.map) return;
            try {
              window.naver.maps.Event.trigger(this.map, 'resize');
              this.map.setCenter(center);
            } catch (e) { console.error(e); }
          });
          return;
        }
        await new Promise(r => setTimeout(r, 100));
      }
    };
    ensureRendered();

    // 컨테이너 크기 변화 대응
    if ('ResizeObserver' in window) {
      this.resizeObserver = new ResizeObserver(() => {
        if (this.disposed || !this.map) return;
        const host = document.getElementById(this.mapId);
        if (!host) return;
        try {
          window.naver.maps.Event.trigger(this.map, 'resize');
        } catch (e) {
          console.error(e);
        }
      });
      try {
        this.resizeObserver.observe(mapDiv);
      } catch (e) {
        console.error(e);
      }
    }

    // bfcache 복귀 시 보정
    this.onPageShowHandler = ev => {
      if (this.disposed || !this.map) return;
      const host = document.getElementById(this.mapId);
      if (!host) return;
      if (ev?.persisted) {
        try {
          window.naver.maps.Event.trigger(this.map, 'resize');
          this.map.setCenter(center);
        } catch (e) {
          console.error(e);
        }
      }
    };
    window.addEventListener('pageshow', this.onPageShowHandler);
  },
  beforeUnmount() {
    // 해제 플래그
    this.disposed = true;

    // 이벤트 제거
    if (this.onPageShowHandler) {
      window.removeEventListener('pageshow', this.onPageShowHandler);
      this.onPageShowHandler = null;
    }

    // 옵저버 해제
    if (this.resizeObserver) {
      try { this.resizeObserver.disconnect(); } catch (e) { console.error(e); }
      this.resizeObserver = null;
    }

    // 타이머 정리
    if (this.resizeFixTimer) {
      clearTimeout(this.resizeFixTimer);
      this.resizeFixTimer = null;
    }

    // 마커/맵 참조 정리
    if (this.marker) {
      try { this.marker.setMap?.(null); } catch (e) { console.error(e); }
      this.marker = null;
    }
    this.map = null;
  },
};
</script>

