}
.naver-map > div { width: 100% !important; height: 100% !important; }
.naver-map canvas { width: 100% !important; height: 100% !important; }

.branch-item p {
  margin: 6px 12px;
  font-size: clamp(15px, 2vw, 18px);
  color: #666;
}

.branch-item a {
  font-size: clamp(15px, 2vw, 18px);
  color: #666;
  cursor: pointer;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 12px;
}

.title h3 {
  margin: 0;
  font-size: clamp(20px, 2vw, 24px);
  color: #333;
}

.clickable-link {
  color: #666;
  text-decoration: underline;
  cursor: pointer;
  pointer-events: auto;
}

.corner-logo-link {
  display: flex;
