<template>
  <transition name="modal">
    <div v-if="show" class="modal-overlay" @click.self="closeModal">
      <div class="modal-container">
        <button class="close-btn" @click="closeModal">×</button>
        <h1>볼더링 주의사항</h1>
        <p class="subtitle">안전한 클라이밍을 위한 필수 가이드</p>

        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progressPercent + '%' }"></div>
        </div>

        <div class="video-screen">
          <transition name="fade" mode="out-in">
            <div :key="currentScene" class="scene">
              <div class="scene-icon">{{ scenes[currentScene].icon }}</div>
              <div class="scene-title">{{ scenes[currentScene].title }}</div>
              <div class="scene-description" v-html="scenes[currentScene].description"></div>
            </div>
          </transition>
        </div>

        <div class="controls">
          <button class="btn-secondary" :disabled="currentScene === 0" @click="prevScene">← 이전</button>
          <button class="btn-secondary" :disabled="currentScene === scenes.length - 1" @click="nextScene">다음 →</button>
        </div>

        <div class="warning-box">
          <span class="icon">⚠️</span>
          <span class="text">부상 예방을 위해 주의사항을 꼭 지켜주세요!</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
  name: 'CautionModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const currentScene = ref(0)

    const scenes = [
      {
        icon: '⭐️',
        title: '볼더링 주의사항',
        description: '안전하고 즐거운 클라이밍을 위해<br>꼭 알아야 할 주의사항을 알려드릴게요!'
      },
      {
        icon: '🤸',
        title: '1. 충분한 준비운동',
        description: '클라이밍 전 최소 10분 이상<br>스트레칭과 준비운동을 해주세요.<br>어깨, 허리를 충분히 풀어주는 것이 중요합니다.'
      },
      {
        icon: '🪂',
        title: '2. 올바른 낙하 자세',
        description: '낙하 시 발부터 착지하고<br>무릎을 살짝 구부려 충격을 흡수하세요.<br>절대 손으로 바닥을 짚지 마세요!'
      },
      {
        icon: '💪',
        title: '3. 체력 관리',
        description: '무리하지 말고 자신의 수준에 맞게<br>천천히 난이도를 높여가세요.<br>충분한 휴식도 중요합니다.'
      },
      {
        icon: '⬜️',
        title: '4. 매트',
        description: '매트 위에 물건을 올려두거나<br>앉아있으면 위험합니다.<br>매트 위에는 한 사람만 있도록 해요.'
      },
      {
        icon: '👟',
        title: '5. 암벽화',
        description: '등반을 할 때는 꼭 암벽화를<br>신고 등반해야합니다.<br>맨발로 등반하지 말아요.'
      },
      {
        icon: '🤝',
        title: '6. 암장 에티켓',
        description: '문제를 풀기 전 루트가 겹치는 지 확인하고<br>대기 순서를 지켜주세요.<br>서로 응원하고 배려하는 문화를 만들어요!'
      },
      {
        icon: '✨',
        title: '안전한 클라이밍!',
        description: '주의사항을 잘 지켜서<br>즐겁고 안전한 볼더링 하세요!'
      }
    ]

    const progressPercent = computed(() => {
      return ((currentScene.value + 1) / scenes.length) * 100
    })

    const closeModal = () => {
      currentScene.value = 0
      emit('close')
    }

    const nextScene = () => {
      if (currentScene.value < scenes.length - 1) {
        currentScene.value++
      }
    }

    const prevScene = () => {
      if (currentScene.value > 0) {
        currentScene.value--
      }
    }

    const handleKeydown = (e) => {
      if (!props.show) return
      
      if (e.key === 'Escape') closeModal()
      if (e.key === 'ArrowRight') nextScene()
      if (e.key === 'ArrowLeft') prevScene()
    }

    onMounted(() => {
      document.addEventListener('keydown', handleKeydown)
    })

    onUnmounted(() => {
      document.removeEventListener('keydown', handleKeydown)
    })

    return {
      currentScene,
      scenes,
      progressPercent,
      closeModal,
      nextScene,
      prevScene
    }
  }
}
</script>

