<template>
  <div class="app-shell">
    <header ref="headerRef">
      <button class="image-button" :style="{ visibility: route.path !== '/' ? 'visible' : 'hidden' }" @click="goBack">
        <img id="back" :src="publicPath + 'images/backwards_icon.png'" alt="🔙">
      </button>
      <h1>{{ headerStore.title }}</h1>
      <button class="image-button" :style="{ visibility: route.path !== '/' ? 'visible' : 'hidden' }" @click="goHome">
        <img id="home" :src="publicPath + 'images/home_icon.png'" alt="🏠">
      </button>
    </header>

    <main class="container" ref="mainRef">
        <router-view :key="route.fullPath" />
    </main>

    <footer>
      <h5>
        <p>Copyright ⓒ eve 🧗‍♀️ - Last Update: 2025.10.15</p>
        <!-- <a href="https://www.instagram.com/move__1224/">Copyright ⓒ move__1224 🧗‍♀️</a> -->
      </h5>
    </footer>

    <ScrollToTop />
  </div>
</template>

<script>
import { useRouter, useRoute } from 'vue-router'
import { ref, onMounted, onBeforeUnmount } from 'vue'
import { headerStore } from './stores/commonStore'
import ScrollToTop from '@/components/ScrollToTop.vue'

export default {
  name: 'App',
  components: {
    ScrollToTop,
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const headerRef = ref(null)
    const mainRef = ref(null)
    const publicPath = process.env.BASE_URL

    const updateMainPadding = () => {
      if (headerRef.value && mainRef.value) {
         const headerHeight = headerRef.value.offsetHeight
         mainRef.value.style.marginTop = headerHeight + 'px'
      }
    }

    const goBack = () => {
      router.back()
    }

    const goHome = () => {
      router.push({name: 'MainView'})
    }

    onMounted(() => {
      updateMainPadding()
      window.addEventListener('resize', updateMainPadding)
    })

    onBeforeUnmount(() => {
      window.removeEventListener('resize', updateMainPadding)
    })

    return {
      route,
      goHome,
      goBack,
      headerStore,
      headerRef,
      mainRef,
      publicPath,
    }
  }
}
</script>

