<template>
  <div>
    <transition name="slide-up">
      <article v-if="!isShowBranch" class="store-item">
        <div class="store-container">
          <ImageCarousel 
            :keyword="store.keyword || 'rock climbing'"
            :storeName="store.title"
          />

          <div class="store-info">
            <div>
              <p id="info-menu">[ 소개 ]</p>
              <p>{{ store.description }}</p>
            </div>
            <div>
              <p id="info-menu">[ 가격 ]</p>
              <table class="price-table">
                <tbody>
                  <tr v-for="(price, index) in store.price" :key="index">
                    <td>{{ price.item }}</td>
                    <td>{{ price.cost }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div>
              <p id="info-menu">[ 난이도 ]</p>
              <div class="colorbar-wrapper">
                <ColorBar id="level-bar" :colors="store.levelColor" height="25px" />
                <span 
                    v-if="settingStore.showTMI"
                    class="colorbar-text"
                    :style="{ left: calculatePosition(store.creatorLevel, store.levelColor.length) }"
                >
                  만든이 레벨
                </span>
              </div>
            </div>
            <div v-if="store.website">
              <p id="info-menu">[ 공식사이트 ]</p>
              <a :href="store.website" target="_blank" rel="noopener noreferrer">{{ store.website }}</a>
            </div>
          </div>
        </div>

        <div class="store-branch">
          <button id="show-branch" @click="showBranches">지점 보기</button>
        </div>
      </article>
    </transition>

    <transition name="slide-down" class="branch-wrapper">
      <div v-if="isShowBranch">
        <transition-group name="branch-list" tag="div" class="branch-container">
          <BranchItem 
            v-for="(branch, index) in sortedBranches" 
            :key="branch.id"
            :branch="branch"
            :storeTitle="store.title"
            :style="{ transitionDelay: `${index * 0.1}s` }"
          />
        </transition-group>
        
        <button id="show-branch" @click="hideBranches">돌아가기</button>
      </div>
    </transition>
  </div>
</template>

<script>
import ImageCarousel from '@/components/ImageCarousel.vue'
import ColorBar from '@/components/ColorBar.vue'
import BranchItem from './BranchItem.vue'
import { settingStore } from '@/stores/commonStore'
import { computed, ref } from 'vue'

export default {
    name: 'StoreItem',
    components: {
      ImageCarousel,
      ColorBar,
      BranchItem
    },
    props: {
      store: {
        type: Object,
        required: true
      }
    },
    setup(props) {
      const isShowBranch = ref(false)

      const sortedBranches = computed(() => {
        return [...props.store.branches].sort((a, b) => {
          if (a.isClosed && !b.isClosed) return 1
          if (!a.isClosed && b.isClosed) return -1
          return a.name.localeCompare(b.name, 'ko-KR')
        })
      })

      const calculatePosition = (index, totalColors) => {
        const colorWidth = 100 / totalColors
        return `${(index + 0.5) * colorWidth}%`
      }

      const showBranches = () => {
        const header = document.querySelector('header') || document.querySelector('.header')
        const headerHeight = header ? header.offsetHeight : 0
        
        window.scrollTo({
          top: -headerHeight,
          behavior: 'auto'
        })
        
        setTimeout(() => {
          isShowBranch.value = true
        }, 100)
      }

      const hideBranches = () => {
        isShowBranch.value = false

        setTimeout(() => {
            window.scrollTo(0, 0)
            document.documentElement.scrollTop = 0
            document.body.scrollTop = 0
        }, 100)
      }

      return {
        settingStore,
        isShowBranch,
        sortedBranches,
        calculatePosition,
        showBranches,
        hideBranches
      }
    }
}
</script>

