<template>
  <transition name="modal">
    <div v-if="show" class="modal-overlay" @click="$emit('close')">
      <div class="modal-content" @click.stop>
        <button class="close-button" @click="$emit('close')">×</button>

        <div v-if="!locationPermission">
          <h2>📍 위치 권한이 필요해요</h2>
          <p>현재 위치를 기반으로 암장을 랜덤으로 추천해드려요.</p>
          <button class="action-button" @click="requestLocation">위치 권한 허용하기</button>
        </div>

        <div v-else-if="loading">
          <h2>🔍 암장 찾는 중...</h2>
          <div class="loading-spinner"></div>
        </div>

        <div v-else-if="noNearbyBranches">
          <h2>😢 근처에 암장이 없어요</h2>
          <p>현재 수도권 지역 암장만 등록되어 있습니다.</p>
          <button class="action-button" @click="$emit('close')">확인</button>
        </div>

        <div v-else-if="recommendedBranch">
          <div class="header-row">
            <div class="toggle-wrapper">
              <ToggleSwitch
                v-model="isNearbyOnly"
                :label="isNearbyOnly ? '근처만' : '전체'"
                @update:modelValue="handleToggleChange"
              />
            </div>
            <h2>🎯 오늘은 여기 어때요?</h2>
          </div>
          <div class="recommendation">
            <h3>{{ recommendedBranch.storeName }} {{ recommendedBranch.name }}</h3>
            <p class="distance">📏 {{ distance }}km 거리</p>
            <p class="address">📍 {{ recommendedBranch.address }}</p>
            <div class="button-group">
              <button class="action-button" @click="goToStore">자세히 보기</button>
              <button class="secondary-button" @click="reroll">다시 뽑기</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ref, watch } from 'vue'
import { stores } from '@/stores/climbStore'
import ToggleSwitch from '@/components/ToggleSwitch.vue'

export default {
  name: 'WhereToGoModal',
  components: {
    ToggleSwitch
  },
  props: {
    show: {
      type: Boolean,
      required: true
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const locationPermission = ref(false)
    const loading = ref(false)
    const userLocation = ref(null)
    const nearbyBranches = ref([])
    const allBranchesWithDistance = ref([])
    const recommendedBranch = ref(null)
    const distance = ref(0)
    const isNearbyOnly = ref(true)
    const noNearbyBranches = ref(false)

    const calculateDistance = (lat1, lon1, lat2, lon2) => {
      const R = 6371
      const dLat = (lat2 - lat1) * Math.PI / 180
      const dLon = (lon2 - lon1) * Math.PI / 180
      const a =
        Math.sin(dLat/2) * Math.sin(dLat/2) +
        Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
        Math.sin(dLon/2) * Math.sin(dLon/2)
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a))
      return R * c
    }

    const findNearbyBranches = (maxDistance = 10) => {
      if (!userLocation.value) return []

      const nearby = []
      stores.forEach(store => {
        store.branches.forEach(branch => {
          if (branch.isClosed || !branch.coordinates) return

          const dist = calculateDistance(
            userLocation.value.latitude,
            userLocation.value.longitude,
            parseFloat(branch.coordinates.lat),
            parseFloat(branch.coordinates.lng)
          )

          if (dist <= maxDistance) {
            nearby.push({
              ...branch,
              storeName: store.title,
              storeId: store.id,
              distance: dist
            })
          }
        })
      })

      return nearby.sort((a, b) => a.distance - b.distance)
    }

    const findAllBranches = () => {
      if (!userLocation.value) return []

      const allBranches = []
      stores.forEach(store => {
        store.branches.forEach(branch => {
          if (branch.isClosed || !branch.coordinates) return

          const dist = calculateDistance(
            userLocation.value.latitude,
            userLocation.value.longitude,
            parseFloat(branch.coordinates.lat),
            parseFloat(branch.coordinates.lng)
          )

          allBranches.push({
            ...branch,
            storeName: store.title,
            storeId: store.id,
            distance: dist
          })
        })
      })

      return allBranches.sort((a, b) => a.distance - b.distance)
    }

    const findClosestBranches = () => {
      return findAllBranches().slice(0, 5)
    }

    const searchAndRecommend = () => {
      const allBranches = findAllBranches()

      if (allBranches.length === 0 || allBranches[0].distance > 100) {
        noNearbyBranches.value = true
        return
      }

      let nearby = findNearbyBranches(5)

      if (nearby.length === 0) {
        nearby = findClosestBranches()
      }

      nearbyBranches.value = nearby
      allBranchesWithDistance.value = allBranches
      noNearbyBranches.value = false
      recommendRandom()
    }

    const recommendRandom = () => {
      const branches = isNearbyOnly.value ? nearbyBranches.value : allBranchesWithDistance.value

      if (branches.length === 0) {
        recommendedBranch.value = null
        return
      }

      const randomIndex = Math.floor(Math.random() * branches.length)
      recommendedBranch.value = branches[randomIndex]
      distance.value = recommendedBranch.value.distance.toFixed(1)
    }

    const handleToggleChange = () => {
      loading.value = true
      setTimeout(() => {
        recommendRandom()
        loading.value = false
      }, 500)
    }

    const requestLocation = () => {
      if (!navigator.geolocation) {
        alert('이 브라우저는 위치 서비스를 지원하지 않습니다.')
        return
      }

      loading.value = true
      navigator.geolocation.getCurrentPosition(
        (position) => {
          userLocation.value = {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          }
          locationPermission.value = true
          searchAndRecommend()
          loading.value = false
        },
        (error) => {
          console.error('위치 권한 오류:', error)
          alert('위치 정보를 가져올 수 없습니다. 브라우저 설정을 확인해주세요.')
          loading.value = false
        }
      )
    }

    const reroll = () => {
      recommendRandom()
    }

    const goToStore = () => {
      if (recommendedBranch.value && recommendedBranch.value.map) {
        window.open(recommendedBranch.value.map, '_blank')
        emit('close')
      }
    }

    watch(() => props.show, (newVal) => {
      if (newVal) {
        if (locationPermission.value && userLocation.value) {
          loading.value = true
          searchAndRecommend()
          loading.value = false
        }
      }
    })

    return {
      locationPermission,
      loading,
      nearbyBranches,
      recommendedBranch,
      distance,
      isNearbyOnly,
      noNearbyBranches,
      requestLocation,
      reroll,
      goToStore,
      handleToggleChange
    }
  }
}
</script>

