<template>
  <div class="main-view">
    <div class="left-menu">
      <p>볼더링이란 무엇인가!</p>
      <button class="menu-item" @click="openWiki">
        볼더링(영어: bouldering)이란 암벽 등반의 한 장르로 로프 없이 바위 덩어리(boulder)를 오르는 행위다. 안전을 위해서 크래쉬 패드(Crash pad, bouldering pad라고도 함)를 바닥에 깔아놓고 하는 것이 보통이다.<br>- 출처: 위키백과
      </button>
    </div>
    <div class="left-menu">
      <p>볼더링 하는 법</p>
      <button class="menu-item scale" @click="openHowToClimb">
        기초부터 차근차근 알아볼까?
      </button>
    </div>
    <div class="left-menu">
      <p>수도권 암장을 몇 군데 소개해줄게</p>
      <div v-for="store in sortedStores" :key="store.id" class="store-row">
        <button 
        class="menu-item scale" 
        @click="goToStore(store)"
      >
        {{ store.title }}
      </button>
      <p v-if="settingStore.showTMI" class="comment">{{ addComment(store.title) }}</p>
      </div>
    </div>
    <div class="left-menu">
      <p>오늘은 어디 가지?</p>
      <button class="menu-item scale" @click="openWhereToGo">
        랜덤으로 추천받자
      </button>
    </div>
    <div class="left-menu">
      <p>안전한 클라이밍을 위한 주의사항</p>
      <button class="menu-item scale" @click="openCaution">
        같이 확인해볼까?
      </button>
    </div>
    <div class="right-menu">
      <p>만든이 TMI Mode</p>
      <div class="menu-item tmi">
        <p>이 모드를 켜면 만든이의<br>TMI도 같이 볼 수 있어.</p>
        <ToggleSwitch 
        v-model="settingStore.showTMI" 
        :label="settingStore.showTMI ? '찾아보는 재미' : '언젠간 켜보자'"
        />
      </div>
      <transition name="expand">
        <div v-if="settingStore.showTMI" class="right-menu tmi-content">
          <p>만든이 소개</p>
          <div class="menu-item tmi">
            <p><b>[ Reach ]</b></p>
            <p>↕ 156cm   ↔ 158cm</p>

            <p><b>[ 클라이밍 기간 ]</b></p>
            <p>2023.10.23 - 현재 진행중</p>

            <p><b>[ 클라이밍 스타일 ]</b></p>
            <p>・ 실내 볼더링 위주</p>
            <p>・ 자연 볼더링 경험 有</p>
            <p>・ 인공 리드 경험 有</p>

            <p><b>[ 강점 ]</b></p>
            <p>밸런스, 크림프</p>

            <p><b>[ 약점 ]</b></p>
            <p>코디, 싸이퍼</p>
          </div>
        </div>
      </transition>
    </div>

    <HowToClimbModal :show="showHowToClimbModal" @close="showHowToClimbModal = false" />
    <CautionModal :show="showCautionModal" @close="showCautionModal = false" />
    <WhereToGoModal :show="showWhereToGoModal" @close="showWhereToGoModal = false" />
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { computed, ref } from 'vue'
import { headerStore, settingStore } from '@/stores/commonStore'
import { stores } from '@/stores/climbStore'
import HowToClimbModal from '@/components/HowToClimbModal.vue'
import CautionModal from '@/components/CautionModal.vue'
import WhereToGoModal from '@/components/WhereToGoModal.vue'
import ToggleSwitch from '@/components/ToggleSwitch.vue'

export default {
  name: 'MainView',
  components: {
    HowToClimbModal,
    CautionModal,
    WhereToGoModal,
    ToggleSwitch,
  },
  setup() {
    headerStore.title = '즐거운 볼더링'

    const router = useRouter()
    const showHowToClimbModal = ref(false)
    const showCautionModal = ref(false)
    const showWhereToGoModal = ref(false)

    const openWiki = () => {
      window.open('https://ko.wikipedia.org/wiki/볼더링', '_blank')
    }

    const openHowToClimb = () => {
      showHowToClimbModal.value = true
    }

    const openWhereToGo = () => {
      showWhereToGoModal.value = true
    }

    const openCaution = () => {
      showCautionModal.value = true
    }

    const goToStore = (store) => {
      console.log('Current store:', store)
      router.push({name: 'StoreInfo', params: { id: store.id }})
    }

    const sortedStores = computed(() => {
      return [...stores].sort((a, b) => {
        const branchDiff = b.branches.length - a.branches.length
        if (branchDiff !== 0) {
          return branchDiff
        }
        
        return a.title.localeCompare(b.title, 'ko-KR')
      })
    })
    
    const addComment = (title) => {
      if(title.includes("더클라임")) {
        return '- 지점이 많아서 회원권 쓰긴 좋지만, 사람도 너무 많다.'
      } else if(title.includes("서울숲")) {
        return '- 지점이 대부분 넓고 깨끗한데, 여기도 사람이 많다.'
      } else if(title.includes("파크")) {
        return '- 여기는 문제가 스태틱한 편이다. 뜯뜯천국'
      } else if(title.includes("손상원")) {
        return '- 문제 스타일이 케바케인듯, 일찍가면 할인해준다.'
      } else if(title.includes("알레")) {
        return '- 여기는 문제가 다이나믹한 편이다. 뛰뛰천국'
      } else if(title.includes("피커스")) {
        return '- 영화관 개조라 층고가 높은 편이고, 화장실이 좋다.'
      }
    }

    return {
      openWiki,
      openHowToClimb,
      openWhereToGo,
      openCaution,
      goToStore,
      sortedStores,
      settingStore,
      addComment,
      showHowToClimbModal,
      showCautionModal,
      showWhereToGoModal,
    }
  }
}
</script>

