<template>
  <div class="image-carousel">
    <div 
      class="carousel-container"
      @touchstart="handleTouchStart"
      @touchmove="handleTouchMove"
      @touchend="handleTouchEnd"
      @mouseenter="pauseAutoSlide"
      @mouseleave="resumeAutoSlide"
    >
      <div v-if="isLoading" class="loading-container">
        <div class="loading-spinner"></div>
      </div>

      <div v-else-if="images.length > 0" class="carousel-content">
        <div 
          class="carousel-track" 
          :style="{ transform: `translateX(-${(currentIndex + 1) * 100}%)` }"
          :class="{ 'no-transition': isTransitioning }"
        >
          <img :src="images[images.length - 1]" alt="클라이밍" class="carousel-image">
          <img 
            v-for="(image, index) in images" 
            :key="index"
            :src="image" 
            alt="클라이밍"
            class="carousel-image"
          >
          <img :src="images[0]" alt="클라이밍" class="carousel-image">
        </div>
      </div>

      <div v-else class="error-container">
        <p class="error-text">이미지를 불러올 수 없습니다</p>
      </div>

      <div v-if="!isLoading && images.length > 0" class="carousel-indicators">
        <button 
          v-for="(_, index) in images" 
          :key="index"
          class="indicator"
          :class="{ active: index === currentIndex }"
          @click="goToSlide(index)"
        ></button>
      </div>
    </div>
  </div>
</template>

<script>
import { getRandomImages } from '@/stores/imageStore'

export default {
  name: 'ImageCarousel',
  props: {
    keyword: {
      type: String,
      default: 'rock climbing'
    },
    storeName: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      images: [],
      currentIndex: 0,
      isTransitioning: false,
      isLoading: true,
      touchStartX: 0,
      touchStartY: 0,
      touchEndX: 0,
      touchEndY: 0,
      minSwipeDistance: 50,
      autoSlideTimeout: null,
      autoSlideDelay: 3000,
      lastSlideTime: 0,
      isUserInteracting: false
    }
  },
  async mounted() {
    await this.loadNewImages()
    this.startAutoSlide()
  },
  beforeUnmount() {
    this.stopAutoSlide()
  },
  methods: {
    async loadNewImages() {
    try {
      this.isLoading = true
      const titleHash = this.stringToHash(this.storeName)
      const timeComponent = Math.floor(Date.now() / 10000)
      const seed = titleHash + timeComponent

      this.images = await getRandomImages(this.keyword, 5, seed)
      this.currentIndex = 0
      this.lastSlideTime = Date.now()
    } catch (error) {
      console.error('이미지 로드 실패:', error)
      this.images = []
    } finally {
      this.isLoading = false
    }
    },
    stringToHash(str) {
      let hash = 0
      for (let i = 0; i < str.length; i++) {
        const char = str.charCodeAt(i)
        hash = ((hash << 5) - hash) + char
        hash = hash & hash
      }
      return Math.abs(hash)
    },
    nextImage() {
      if (this.images.length === 0) return
      this.lastSlideTime = Date.now()
      this.currentIndex++
    
      if (this.currentIndex >= this.images.length) {
        setTimeout(() => {
        this.isTransitioning = true
        this.currentIndex = 0
        setTimeout(() => { this.isTransitioning = false }, 50)
        }, 300)
      }
    },
    prevImage() {
      if (this.images.length === 0) return
      this.lastSlideTime = Date.now()
      this.currentIndex--
    
      if (this.currentIndex < 0) {
        setTimeout(() => {
        this.isTransitioning = true
        this.currentIndex = this.images.length - 1
        setTimeout(() => { this.isTransitioning = false }, 50)
        }, 300)
      }
    },
    goToSlide(index) {
      this.lastSlideTime = Date.now()
      this.currentIndex = index
      this.scheduleNextSlide()
    },
    scheduleNextSlide() {
      this.stopAutoSlide()
      if (this.images.length <= 1 || this.isUserInteracting) return
    
      const timeElapsed = Date.now() - this.lastSlideTime
      const timeRemaining = Math.max(0, this.autoSlideDelay - timeElapsed)
    
      this.autoSlideTimeout = setTimeout(() => {
        if (!this.isUserInteracting) {
          this.nextImage()
          this.scheduleNextSlide()
        }
      }, timeRemaining)
    },
    startAutoSlide() {
      this.isUserInteracting = false
      this.scheduleNextSlide()
    },
    stopAutoSlide() {
      if (this.autoSlideTimeout) {
        clearTimeout(this.autoSlideTimeout)
        this.autoSlideTimeout = null
      }
    },
    pauseAutoSlide() {
      this.isUserInteracting = true
      this.stopAutoSlide()
    },
    resumeAutoSlide() {
      this.isUserInteracting = false
      this.startAutoSlide()
    },
    handleTouchStart(e) {
      if (this.isLoading) return
      this.touchStartX = e.touches[0].clientX
      this.touchStartY = e.touches[0].clientY
      this.isUserInteracting = true
      this.stopAutoSlide()
    },
    handleTouchMove(e) {
      if (this.isLoading) return
      const touchX = e.touches[0].clientX
      const touchY = e.touches[0].clientY
      const deltaX = Math.abs(touchX - this.touchStartX)
      const deltaY = Math.abs(touchY - this.touchStartY)
    
      if (deltaX > deltaY && deltaX > 10) {
        e.preventDefault()
      }
    },
    handleTouchEnd(e) {
      if (this.isLoading) return
      this.touchEndX = e.changedTouches[0].clientX
      this.touchEndY = e.changedTouches[0].clientY
      this.handleSwipe()
      this.isUserInteracting = false
      this.startAutoSlide()
    },
    handleSwipe() {
      const deltaX = this.touchStartX - this.touchEndX
      const deltaY = this.touchStartY - this.touchEndY
      const absX = Math.abs(deltaX)
      const absY = Math.abs(deltaY)
    
      if (absX > absY && absX > this.minSwipeDistance) {
        if (deltaX > 0) {
        this.nextImage()
        } else {
        this.prevImage()
        }
      }
    }
  }
}
</script>

<style scoped>
.carousel-track.no-transition {
    transition: none !important;
}

.image-carousel {
    position: relative;
    width: 100%;
    height: 300px;
}

.carousel-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    touch-action: pan-y;
}

.carousel-content {
    width: 100%;
    height: 100%;
    position: relative;
}

.carousel-track {
    display: flex;
    width: 100%;
    height: 100%;
    transition: transform 0.3s ease;
}

.carousel-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    flex-shrink: 0;
    user-select: none;
    pointer-events: none;
}

.loading-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: #f8f9fa;
    position: absolute;
    top: 0;
    left: 0;
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid #e0e0e0;
    border-top: 4px solid #007bff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 12px;
}

.error-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: #f8f9fa;
    position: absolute;
    top: 0;
    left: 0;
}

.error-text {
    color: #999;
    font-size: 14px;
    margin: 0;
}

.carousel-indicators {
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    gap: 8px;
    z-index: 2;
}

.indicator {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    border: none;
    background: rgba(255, 255, 255, 0.6);
    cursor: pointer;
    transition: background 0.2s ease;
}

.indicator.active {
    background: rgba(255, 255, 255, 1);
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
</style>